                @table-select-all="hdrTableSelectAll"
                @table-selection-change="hdrTableSelectionChange"
                @table-cell-click="hdrTableCellClick"
                @table-row-dblclick="hdrTableRowDbclick"
                @table-header-click="hdrTableHeaderClick"
                @table-sort-change="hdrTableSortChange"
                @table-current-change="hdrTableCurrentChange"
                @pagination-size-change="hdrPaginationSizeChange"
                @pagination-current-change="hdrPaginationCurrentChange"
                @table-buttons-event="hdrTableButtonsEvent"
                @upload-success="hdrUploadSuccess"
                @del-uploaded="hdrDelUploaded"
                @down-load-attachment="hdrDownLoadAttachment"
            ></cool-table-group>
        </el-main>
        <el-footer v-if="dltTableData.length" height="260px">
            <el-tabs ref="tabs" type="border-card" v-model="activeName">
                <el-tab-pane
                    v-for="dltTab in dltTableData"
                    :label="dltTab.label"
                    :name="dltTab.name"
                >
                    <span slot="tab.label">{{dltTab.label}}</span>
                    <el-container>
                        <el-header height="auto">
                            <slot name="footerTableSlot" v-if="dltTab.showFooterTableSlot"></slot>
                            <cool-button-group
                                v-if="dltTab.buttons && activeName === dltTab.name"
                                :buttons="dltTab.buttons"
                                @button-click="(value)=> dltTabButtonsEvent(value,dltTab.label)"
                            ></cool-button-group>
                        </el-header>
                        <el-main>
                            <cool-table-group
                                ref="dltTable"
                                :urlUploadAttachment="dltTab.urlUploadAttachment"
                                :uploadTool="dltTab.uploadTool"
                                :showDownload="dltTab.showDownload"
                                :uploadChooseText="dltTab.uploadChooseText"
                                :upbtnUploadText="dltTab.upbtnUploadText"
                                :uploadDeleteText="dltTab.uploadDeleteText"
