                                :width="dltTab.width"
                                :height="dltTab.height"
                                :maxHeight="dltTab.maxHeight"
                                :fit="dltTab.fit"
                                :rowKey="dltTab.rowKey"
                                :border="dltTab.border"
                                :data="dltTab.data"
                                :columns="dltTab.columns"
                                :total="dltTab.total"
                                :layout="dltTab.layout"
                                :pageSize="dltTableData.pageSize"
                                :currentPage="dltTableData.currentPage"
                                :context="dltTab.context"
                                :showHeader="dltTab.showHeader"
