            </el-tabs>
        </el-footer>
    </el-container>
</template>

<script>
import coolTableGroup from './cool-table.vue'
const component = {
    name: 'table-views',
    components: {
        coolTableGroup,
    },
    data: function() {
        return {
            activeName: this.dltTableData.length
                ? this.dltTableData[0].name
                : '',
        }
    },
    props: {
        // table主表的数据
        hdrTableData: {
            type: Object,
            default: function() {
                return {}
            },
        },
        // 是否显示从表
        showDltTable: {
            type: Boolean,
            default: true,
        },
        //table从表的数据
        dltTableData: {
            type: Array,
            default: function() {
                return []
            },
        },
    },
    install: (Vue, options) =>
        Vue.component(
            (options ? options.prefix : '') + component.name,
            component,
        ),
    methods: {
        // table主表方法
        hdrTableRowClick(arg) {
            this.$emit('hdr-row-click', arg)
        },
        hdrTableSelect(arg) {
            this.$emit('hdr-select', arg)
        },
        hdrTableSelectAll(arg) {
            this.$emit('hdr-select-all', arg)
        },
        hdrTableSelectionChange(arg) {
            this.$emit('hdr-select-change', arg)
        },
        hdrTableCellClick(arg) {
            this.$emit('hdr-cell-click', arg)
        },
        hdrTableRowDbclick(arg) {
            this.$emit('hdr-row-dbclick', arg)
        },
        hdrTableHeaderClick(arg) {
            this.$emit('hdr-header-click', arg)
        },
        hdrTableSortChange(arg) {
            this.$emit('hdr-sort-change', arg)
        },
        hdrTableCurrentChange(arg) {
            this.$emit('hdr-current-change', arg)
        },
        hdrPaginationSizeChange(arg) {
            this.$emit('hdr-page-size-change', arg)
        },
        hdrPaginationCurrentChange(args) {
            this.$emit('hdr-pagination-current-change', args)
        },
        hdrClearSelectionOuter() {
            this.$refs.hdrTable.clearSelectionOuter()
        },
        hdrClearCurrentRow() {
            this.$refs.hdrTable.clearCurrentRow()
        },
        // 主表button组件的方法
        hdrTableButtonsEvent(arg) {
            this.$emit('hdr-table-buttons-event', arg)
        },
        // 主表上传组件的方法
        hdrUploadSuccess(response, file, fileList) {
            this.$emit('hdr-upload-success', response, file, fileList)
        },
        hdrDelUploaded() {
            this.$emit('hdr-del-uploaded')
        },
        hdrDownLoadAttachment() {
            this.$emit('hdr-down-load-attachment')
        },

        // 从表表格方法
        dltTableRowClick(arg) {
            this.$emit('dlt-row-click', arg)
        },
        dltTableSelect(arg) {
            this.$emit('dlt-select', arg)
        },
        dltTableSelectAll(arg) {
            this.$emit('dlt-select-all', arg)
        },
        dltTableSelectionChange(arg) {
            this.$emit('dlt-select-change', arg)
        },
        dltTableCellClick(arg) {
            this.$emit('dlt-cell-click', arg)
        },
        dltTableRowDbclick(arg) {
            this.$emit('dlt-row-dbclick', arg)
        },
        dltTableHeaderClick(arg) {
            this.$emit('dlt-header-click', arg)
        },
        dltTableSortChange(arg) {
            this.$emit('dlt-sort-change', arg)
        },
        dltTableCurrentChange(arg) {
            this.$emit('dlt-current-change', arg)
        },
        dltPaginationSizeChange(arg) {
            this.$emit('dlt-page-size-change', arg)
        },
        dltPaginationCurrentChange(args) {
            this.$emit('dlt-pagination-current-change', args)
        },
        dltClearSelectionOuter() {
            this.$refs.dltTable.clearSelectionOuter()
        },
        dltClearCurrentRow() {
            this.$refs.dltTable.clearCurrentRow()
        },
        // 从表button组件的方法
        dltTableButtonsEvent(arg) {
            this.$emit('dlt-table-buttons-event', arg)
        },
        // 从表上传组件方法
        dltUploadSuccess(response, file, fileList) {
            this.$emit('dlt-upload-success', response, file, fileList)
        },
        dltDelUploaded() {
            this.$emit('dlt-del-uploaded')
        },
        dltDownLoadAttachment() {
            this.$emit('dlt-down-load-attachment')
        },
        dltTabButtonsEvent(arg, label) {
            this.$emit('dlt-button-click', arg, label)
        },
    },
}

export default component
</script>

