<template>
<div class="cool-query">
  <el-container>  
  <el-aside width="220px" v-show="isShowQuery" style="overflow-y: hidden;height:100%;">
    <div style="text-align:center;">{{queryText}}</div>
    <el-main style="font-size:13px;padding:10px;">
      <el-row :gutter="5" type="flex" justify="center" align="middle" style="margin-bottom:10px;text-align:center" v-for="(item,index) in originCondition" :key="index">
        <el-col :span="10">{{item.name}}</el-col>
        <el-col :span="14">
          <el-input clearable :size="size" v-model="item.value" @change="updateCondition" v-if="item.form=='input'" :disabled="item.disabled" :readonly="item.readonly"></el-input>
          <el-row v-if="item.form=='radio'">
            <el-col :span="12">
              <el-radio label="true" v-model="item.value" :size="size" @change="updateCondition" v-if="item.form=='radio'">是</el-radio>
            </el-col>
            <el-col :span="12">
              <el-radio label="false" v-model="item.value" :size="size" @change="updateCondition" v-if="item.form=='radio'">否</el-radio>
              </el-radio>
            </el-col>
          </el-row>
          <el-select v-model="item.value" v-if="item.form=='select'" :size="size" clearable @change="updateCondition">
            <el-option v-for="option in item.options" :key="option.value" :value="option.value" :label="option.label"></el-option>
          </el-select>
          <el-select v-model="item.value" v-if="item.form=='remoteSelect'" @change="updateCondition" :size="size" filterable remote :remote-method="remoteMethod" :loading="item.loading" clearable>
            <el-option v-for="(ele,index) in item.options" :label="ele.label" :value="ele.value" :key="index"></el-option>
          </el-select>
          <el-date-picker style="width:100%;font-size:10px" v-model="item.value[0]" type="date" :size="size" @change="updateCondition" v-if="item.form=='date'" value-format="yyyy-MM-dd HH:mm:ss"><br></el-date-picker>
          <el-date-picker style="width:100%;font-size:10px" v-model="item.value[1]" type="date" :size="size" @change="updateCondition" v-if="item.form=='date'" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
          <el-input-number v-model="item.value" :size="size" :controls="true" @change="updateCondition" v-if="item.form=='number'" style="width:100%"></el-input-number>
        </el-col>
      </el-row>
    </el-main>
  </el-aside>
  <el-aside width="auto" style="z-index:1">
      <el-button size="mini" class="menu-collapse-button" @click="isShowQuery=!isShowQuery"></el-button>
  </el-aside>
  </el-container>
</div>
</template>

<script>
import {isArrOrObj} from '@/utils/axios'
const component = {
  name: 'query-ago',
  props: {
    diyGetCondition: Boolean,
    originCondition: {
      type: [Object, Array],
      default: function() {
        return {} || []
      }
    },
    queryText: {
      type: String,
      default: '查询条件'
    }
  },
  install: (Vue, options) =>
    Vue.component(
      (options ? options.prefix : '') + component.name,
      component,
    ),
  data() {
    return {
      size: 'mini',
      condition: [],
      loading: false,
      isShowQuery:true,
    }
  },
  mounted() {
    this.updateCondition()
  },
  methods: {
    // 判断originCondition是数组还是对象的方法
    // isArrOrObj(obj) {
    //   if (Array.isArray) {
    //     return Array.isArray(obj);
    //   } else {
    //     return Object.prototype.toString.call(obj) === "[object Array]";
    //   }
    // },
    // 当originCondition为对象时 执行的方法
    objFn() {
      this.condition = []
      var obj = {}
      for (var prop in this.originCondition) {
        this.condition.push(this.originCondition[prop])
        this.condition.forEach(p => {
          obj[p.fieldName] = p.value
        })
      }
      this.$emit('get-condition', obj)
    },
    // objFn(){
    //   this.condition = []
    //   var obj={}
    //   for (var prop in this.originCondition) {
    //       this.condition.push(this.originCondition[prop])
    //   }
    //   this.condition = this.condition.map(item=>{
    //       return {
    //             "FieldName":item.fieldName,
    //             "TableName":"[InfoTable]",
    //             "Value":[{"value":item.value}],
    //             "TableRelationMode":"AND",
    //             "Mode":"等于",
    //             "DataType":"string"
    //           }
    //         }).filter(item=>item.Value[0].value != '')
    //       console.log(this.condition)
    //   this.$emit('get-condition', this.condition)
    // },
    // 当originCondition是数组试 执行的方法 这个主要是为了太兴项目的进出仓部分
    arrFn() {
      let arr = []
      let queryArr = []
      this.originCondition.forEach(p => {
        if(Array.isArray(p.value)){
          if(p.value[0] && p.value[0].length>0 || p.value[1] && p.value[1].length>0) queryArr.push(p)
          else return
        } else if (p.value.length != 0) {
          queryArr.push(p)
        }
      })
      arr = queryArr.map(p => {
        let obj = {}
        obj.key = p.fieldName
        if (p.form !== 'date') {
          obj.value = p.value.trim()
        } else {
          obj.value = p.value
        }
        return obj
      })
      this.$emit('get-condition-ware', arr)
    },
    updateCondition() {
      if(this.diyGetCondition){
        console.log('diyGetCondition');
        this.$emit('get-condition')
      } else {
        isArrOrObj(this.originCondition) == true ? this.arrFn() : this.objFn()
      }
    },
    remoteMethod(query) {
      this.$emit('remote-method', query)
    },
  }
}

export default component
</script>

<style scoped>
.menu-collapse-button {
  padding: 3px !important;
  height: 100% !important;
}
</style>
