<template>
<el-container>
  <el-header height="auto" v-if="buttons.length">
    <cool-button-group :buttons="buttons" @button-click="buttonsevent" />
  </el-header>
  <slot name="headerSlot"></slot>
  <el-main>
    <el-container>
      <cool-query-ago v-if="originCondition" :origin-condition="originCondition" :queryText="queryText" @get-condition-ware="getConditionWare" @get-condition="getCondition" @remote-method="remoteMethod" :diy-get-condition="diyGetCondition" />
      <table-view ref="tableView" :hdrTableData="hdrTableData" :dltTableData="dltTableData" @hdr-row-click="hdrTableRowClick" @hdr-select="hdrTableSelect" @hdr-select-all="hdrTableSelectAll" @hdr-select-change="hdrTableSelectionChange"
        @hdr-cell-click="hdrTableCellClick" @hdr-row-dbclick="hdrTableRowDbclick" @hdr-header-click="hdrTableHeaderClick" @hdr-sort-change="hdrTableSortChange" @hdr-current-change="hdrTableCurrentChange" @hdr-page-size-change="hdrPaginationSizeChange"
        @hdr-pagination-current-change="hdrPaginationCurrentChange" @hdr-table-buttons-event="hdrTableButtonsEvent" @hdr-upload-success="hdrUploadSuccess" @hdr-del-uploaded="hdrDelUploaded" @hdr-down-load-attachment="hdrDownLoadAttachment"
        @dlt-row-click="dltTableRowClick" @dlt-select="dltTableSelect" @dlt-select-all="dltTableSelectAll" @dlt-select-change="dltTableSelectionChange" @dlt-cell-click="dltTableCellClick" @dlt-row-dbclick="dltTableRowDbclick" @dlt-header-click="dltTableHeaderClick"
        @dlt-sort-change="dltTableSortChange" @dlt-current-change="dltTableCurrentChange" @dlt-page-size-change="dltPaginationSizeChange" @dlt-pagination-current-change="dltPaginationCurrentChange" @dlt-table-buttons-event="dltTableButtonsEvent"
        @dlt-upload-success="dltUploadSuccess" @dlt-del-uploaded="dltDelUploaded" @dlt-down-load-attachment="dltDownLoadAttachment" @dlt-button-click="dltTabButtonsEvent">
          <template slot="footerTableSlot">
                <slot name="footerTableSlot"></slot>
              </template>
        </table-view>
    </el-container>
  </el-main>
  <cool-multi-dialog ref="multiDialog" v-if="dialogs.length" :dialogs="dialogs" @back-event="backEvent" @save-event="saveEvent" />
</el-container>
</template>

<script>
// import buttonGroup from './cool-button.vue'
// import query from './cool-query.vue'
import tableView from './cool-table-page'
// import multiDialog from './cool-multi-dialog'
const component = {
  name: 'views',
  components: {
    // buttonGroup,
    // query,
    tableView
    // multiDialog
  },
  props: {
    diyGetCondition: Boolean,
    // buttons数据
    buttons: {
      type: Array,
      default: function() {
        return []
      }
    },
    // 查询条件组件的title文字
    queryText: String,
    originCondition: [Object, Array],
    // table主表的数据
    hdrTableData: {
      type: Object,
      default: function() {
        return {}
      }
    },
    //table从表的数据
    dltTableData: {
      type: Array,
      default: function() {
        return []
      }
    },
    // 弹窗数据
    dialogs: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  methods: {
    remoteMethod(query) {
      this.$emit('remote-method', query)
    },

    buttonsevent(arg) {
      this.$emit('button-click', arg)
    },
    getConditionWare(arg) {
      this.$emit('get-condition-ware', arg)
    },
    getCondition(arg) {
      this.$emit('get-condition', arg)
    },
    // table主表方法
    hdrTableRowClick(arg) {
      this.$emit('hdr-row-click', arg)
    },
    hdrTableSelect(arg) {
      this.$emit('hdr-select', arg)
    },
    hdrTableSelectAll(arg) {
      this.$emit('hdr-select-all', arg)
    },
    hdrTableSelectionChange(arg) {
      this.$emit('hdr-select-change', arg)
    },
    hdrTableCellClick(arg) {
      this.$emit('hdr-cell-click', arg)
    },
    hdrTableRowDbclick(arg) {
      this.$emit('hdr-row-dbclick', arg)
    },
    hdrTableHeaderClick(arg) {
      this.$emit('hdr-header-click', arg)
    },
    hdrTableSortChange(arg) {
      this.$emit('hdr-sort-change', arg)
    },
    hdrTableCurrentChange(args) {
      this.$emit('hdr-current-change', args)
    },
    hdrPaginationSizeChange(args) {
      this.$emit('hdr-pagination-size-change', args)
    },
    hdrPaginationCurrentChange(args) {
      this.$emit('hdr-pagination-current-change', args)
    },
    hdrClearSelectionOuter() {
      this.$refs.tableView.hdrClearSelectionOuter();
    },
    hdrClearCurrentRow() {
      this.$refs.tableView.hdrClearCurrentRow();
    },
    // 主表button组件的方法
    hdrTableButtonsEvent(arg) {
      this.$emit('hdr-buttons-event', arg)
    },
    // 主表上传组件的方法
    hdrUploadSuccess(response, file, fileList) {
      this.$emit('hdr-upload-success', response, file, fileList)
    },
    hdrDelUploaded() {
      this.$emit('hdr-del-uploaded')
    },
    hdrDownLoadAttachment() {
      this.$emit('hdr-down-load-attachment')
    },

    // 从表表格方法
    dltTableRowClick(arg) {
      this.$emit('dlt-row-click', arg)
    },
    dltTableSelect(arg) {
      this.$emit('dlt-select', arg)
    },
    dltTableSelectAll(arg) {
      this.$emit('dlt-select-all', arg)
    },
    dltTableSelectionChange(arg) {
      this.$emit('dlt-select-change', arg)
    },
    dltTableCellClick(arg) {
      this.$emit('dlt-cell-click', arg)
    },
    dltTableRowDbclick(arg) {
      this.$emit('dlt-row-dbclick', arg)
    },
    dltTableHeaderClick(arg) {
      this.$emit('dlt-header-click', arg)
    },
    dltTableSortChange(arg) {
      this.$emit('dlt-sort-change', arg)
    },
    dltTableCurrentChange(arg) {
      this.$emit('dlt-current-change', arg)
    },
    dltPaginationSizeChange(arg) {
      this.$emit('dlt-pagination-size-change', arg)
    },
    dltPaginationCurrentChange(arg) {
      this.$emit('dlt-pagination-current-change', arg)
    },
    dltClearSelectionOuter() {
      this.$refs.tableView.dltClearSelectionOuter();
    },
    dltClearCurrentRow() {
      this.$refs.tableView.dltClearCurrentRow();
    },
    // 从表button组件的方法
    dltTableButtonsEvent(arg) {
      this.$emit('dlt-buttons-event', arg)
    },
    // 从表上传组件的方法
    dltUploadSuccess(response, file, fileList) {
      this.$emit('dlt-upload-success', response, file, fileList)
    },
    dltDelUploaded() {
      this.$emit('dlt-del-uploaded')
    },
    dltDownLoadAttachment() {
      this.$emit('dlt-down-load-attachment')
    },
    closedialog() {
      this.$emit('closedialog')
    },
    backEvent() {
      this.$emit('back-event')
    },
    saveEvent() {
      this.$emit('save-event')
    },
    dltTabButtonsEvent(arg,label) {
      this.$emit('dlt-button-click', arg , label)
    },
  },
  install: (Vue, options) =>
    Vue.component(
      (options ? options.prefix : '') + component.name,
      component,
    ),
}

export default component
</script>

<style scoped>

</style>
