<template>
  <el-dialog
        ref="quickEdit"
        :top.sync="top"
        :visible.sync="visible"
        :fullscreen.sync="fullscreen"
        :width="width"
        :close-on-click-modal="false"
        :close-on-press-escape="true"
        :show-close="false"
        @close="closedialog"
        append-to-body
        :before-close="beforeClose"
    >
        <div slot="title">
            {{title}}
            <el-button-group style="float: right">
                <el-button
                    :disabled="fullscreen"
                    style="width:24px; padding:4px;"
                    size="mini"
                    icon="el-icon-d-caret"
                    @click="collapsedClicked"
                ></el-button>
                <el-button
                    style="width:24px; padding:4px;"
                    size="mini"
                    icon="el-icon-tickets"
                    type="primary"
                    @click="maxmizeClicked"
                ></el-button>
                <el-button
                    style="width:24px; padding:4px;"
                    size="mini"
                    icon="el-icon-close"
                    type="danger"
                    @click="closeClicked"
                ></el-button>
            </el-button-group>
        </div>
        <el-collapse-transition>
            <div v-show="!collapsed">
              <div  :style="borderStyle">
                <cool-form-view ref="quickFormView" :form-items="formItems" @update-form='updateForm'  :size="size" :inline="inline" :label-width="labelWidth" @keyup-enter="keyEnter" @tab-event="tabEvent"></cool-form-view>
              </div>
            </div>
        </el-collapse-transition>
        <div slot="footer">
            <transition name="el-fade-in-linear">
                <div v-show="!collapsed">
                    <cool-save-button
                        v-if="showSaveButton"
                        ref="saveButton"
                        :backBtnIcon="backBtnIcon"
                        :saveBtnIcon="saveBtnIcon"
                        :BtnSize="BtnSize"
                        :backBtnText="backBtnText"
                        :saveBtnText="saveBtnText"
                        :backBtnType="backBtnType"
                        :saveBtnType="saveBtnType"
                        :saveBtnDisabled="saveBtnDisabled"
                        @back-event="backEvent"
                        @save-event="saveEvent"
                        @key-down-event="keyDownEvent"
                        @key-up-event="keyUpEvent"
                    />
                </div>
            </transition>
        </div>
    </el-dialog>
</template>

<script>
import saveButtonMixin from '../mixins/saveButtonMixin.js'
import coolFormMixin from '../mixins/coolFormMixin.js'
import Vue from 'vue'
const component = {
    name: 'quick-edit',
    mixins: [saveButtonMixin,coolFormMixin],
    props: {
      formItems: {
        type:[Object,Array],
        default:function(){
          return {} || []
        }
      },
      borderStyle:{
        type:String,
        default:'border:1px solid #DCDFE6'
      },
      size: String,
      inline: {
            type: Boolean,
            default: true,
        },
      itemWidth: String,
      itemStyle: Object,
      labelWidth: String,
      rules: Object,
      labelPosition: String,
      name: String,
      visible: Boolean,
      width: String,
      iframeHeight: String,
      title: {
        type:String,
        default:'编辑框'
      },
      saveBtnText:{
        type:String,
        default:"确认"
      },
      src: String,
      top: String,
      showSaveButton: {
            type: Boolean,
            default: true,
        },
      allData:{
        type:Array,
        default:function(){
          return []
        }
      },
      currentEditData:{
        type:[Object,Array],
        default:function(){
          return {} || []
        }
      }
    },
    data() {
        return {
            collapsed: false,
            fullscreen: false,
            currentIndex:0,
            lastData:false
        }
    },
    install: (Vue, options) =>
        Vue.component(
            (options ? options.prefix : '') + component.name,
            component,
        ),
    beforeUpdate(){
      // 进来弹窗 焦点在第一个可编辑的输入框
      this.currentIndex = this.allData.indexOf(this.currentEditData)
      Vue.nextTick(()=>{
           this.autoFocus()
        })
    },
    methods: {
           autoFocus(){
              let domArr = this.$refs.quickFormView.$children[0].$el.getElementsByClassName('el-input__inner')
              domArr[Array.from(domArr).findIndex(item=>{return !item.disabled})].focus()
           },
            // 当数据编辑到最后一条 弹出提示框
           comfirmEditData() {
               this.$confirm('数据已经编辑到最后一条了!', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then(() => {
                  this.closeClicked()
                }).catch(() => {
                  this.currentIndex = this.allData.length - 1
                }).finally(()=>{
                  setTimeout(()=>{this.lastData = false},800)
                });
            },
         updateForm(val,arg,label){
            if(val){
              for(let i in val){
              this.allData[this.currentIndex][i] = val[i]
              }
            }
         },
         keyEnter(e) {
          if(!this.lastData) {
              this.changeEditData()
              this.$emit('quick-save-event')
            }
          },
          keyUpEvent(e){
            // 保存按钮
            if(e.code == "Enter"  && e.target.id == "JUSTSAVE" || e.code == "NumpadEnter" && e.target.id == "JUSTSAVE")this.changeEditData()
            // 返回按钮
            if(e.code == "Enter" && e.target.id == "JUSTBACK" || e.code == "NumpadEnter" && e.target.id == "JUSTBACK")this.backEvent()
          },
          changeEditData(){
            if(this.$refs.quickFormView.validateForm()){
                this.currentIndex++
                if(this.currentIndex >= this.allData.length){
                  this.lastData = true
                  if(this.currentIndex == this.allData.length)this.comfirmEditData()
                }else{
                  this.$refs.quickFormView.clearForm()
                  this.$refs.quickFormView.resetForm()
                  this.formItems.form.forEach(p=>{
                    for(let i in this.allData[this.currentIndex]){
                      if(i == p.name)p.value = this.allData[this.currentIndex][i]
                      }
                  })
                 this.autoFocus()
                }
            }
          },
          keyDownEvent(e){

          },
          tabEvent(e){
            this.$refs.quickFormView.updateForm()
          },
        collapsedClicked: function() {
            this.collapsed = !this.collapsed
            this.$emit('update:collapsed', !this.collapsed) //emit for sync
        },
        maxmizeClicked: function() {
            this.fullscreen = !this.fullscreen
            this.$emit('update:fullscreen', !this.fullscreen) //emit for sync
            this.$emit('update:collapsed', false) //emit for sync
        },
        closeClicked: function() {
            this.$emit('update:visible', !this.visible) //emit for sync
        },
        closedialog() {
            this.collapsed = false
            this.fullscreen = false
            this.$emit('closedialog')
            // this.$emit('update:src', '')  //emit for sync
        },
        backEvent() {
            this.collapsed = false
            this.fullscreen = false
            this.$refs.quickFormView.clearForm()
            this.$refs.quickFormView.resetForm()
            this.closeClicked()
            this.$emit('quick-back-event')
        },
        saveEvent() {
            this.collapsed = false
            this.fullscreen = false
            this.changeEditData()
            this.$emit('quick-save-event')
        },
        beforeClose() {
            this.$emit('before-close')
        }
    }
}

export default component
</script>

<style scoped>
</style>
