<template>
    <el-dialog
        :top.sync="top"
        :visible.sync="visible"
        :fullscreen.sync="fullscreen"
        :width="width"
        :close-on-click-modal="false"
        :show-close="false"
        @close="closedialog"
        append-to-body
        :before-close="beforeClose"
    >
        <div slot="title">
            {{title}}
            <el-button-group style="float: right">
                <el-button
                    :disabled="fullscreen"
                    style="width:24px; padding:4px;"
                    size="mini"
                    icon="el-icon-d-caret"
                    @click="collapsedClicked"
                ></el-button>
                <el-button
                    style="width:24px; padding:4px;"
                    size="mini"
                    icon="el-icon-tickets"
                    type="primary"
                    @click="maxmizeClicked"
                ></el-button>
                <el-button
                    style="width:24px; padding:4px;"
                    size="mini"
                    icon="el-icon-close"
                    type="danger"
                    @click="closeClicked"
                ></el-button>
            </el-button-group>
        </div>
        <el-collapse-transition>
            <div v-show="!collapsed" style="overflow: hidden;border:1px solid #DCDFE6;">
                <iframe
                    ref="iframe"
                    :src="src"
                    :id="name"
                    :style="{'width':'100%','border':'none','height':(fullscreen?'90vh':'80vh'),'vertical-align':'bottom'}"
                ></iframe>
            </div>
        </el-collapse-transition>
        <div slot="footer">
            <transition name="el-fade-in-linear">
                <div v-show="!collapsed">
                    <cool-save-button
                        v-if="showSaveButton"
                        ref="saveButton"
                        :backBtnIcon="backBtnIcon"
                        :saveBtnIcon="saveBtnIcon"
                        :BtnSize="BtnSize"
                        :backBtnText="backBtnText"
                        :saveBtnText="saveBtnText"
                        :backBtnType="backBtnType"
                        :saveBtnType="saveBtnType"
                        :saveBtnDisabled="saveBtnDisabled"
                        @back-event="backEvent"
                        @save-event="saveEvent"
                    />
                </div>
            </transition>
        </div>
    </el-dialog>
</template>

<script>
import saveButtonMixin from '../../mixins/saveButtonMixin.js'
const component = {
    name: 'cool-dialog',
    mixins: [saveButtonMixin],
    props: {
        name: String,
        visible: Boolean,
        width: String,
        iframeHeight: String,
        fullscreen: Boolean,
        collapsed: Boolean,
        title: String,
        src: String,
        top: String,
        showSaveButton: {
            type: Boolean,
            default: false,
        },
    },
    install: (Vue, options) =>
        Vue.component(
            (options ? options.prefix : '') + component.name,
            component,
        ),
    methods: {
        collapsedClicked: function() {
            this.$emit('update:collapsed', !this.collapsed) //emit for sync
        },
        maxmizeClicked: function() {
            this.$emit('update:fullscreen', !this.fullscreen) //emit for sync
            this.$emit('update:collapsed', false) //emit for sync
        },
        closeClicked: function() {
            this.$emit('update:visible', !this.visible) //emit for sync
        },
        closedialog() {
            this.$emit('closedialog')
            // this.$emit('update:src', '')  //emit for sync
        },
        backEvent() {
            this.$emit('back-event')
        },
        saveEvent() {
            this.$emit('save-event')
        },
        beforeClose() {
            this.$emit('before-close')
        },
    },
}

export default component
</script>

<style scoped>
</style>
