<template>
    <el-dialog :top.sync="top" :visible.sync="visible" :width="width" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false" @close="closedialog" append-to-body>
    <div slot="title">
        {{editDialogTitle}}
        <el-button-group style="float: right">
            <el-button :style="closeBtnStyle" :size="closeBtnMini" icon="el-icon-close" :type="closeBtnType" @click="closeClicked"></el-button>
        </el-button-group>
    </div>
      <el-container :style="containerStyle"  height="auto">
        <el-main  height="auto">
            <slot name="coolForm"></slot>
          <cool-form-view ref="coolForm" :form-items="formItems"  :inline="inline" :size="size" :itemWidth="itemWidth" :itemStyle="itemStyle" :labelWidth="labelWidth" :rules="rules" :labelPosition="labelPosition" @update-form="updateForm" @input-button-event="mixInputButtonEvent" @click-events="itemClick" @select="handleSelect" @query-search="querySearch" @keyup-enter="keyupEnter" @keyup-up="keyupUp" @keyup-down="keyupDown">
         </cool-form-view>
        </el-main>
        <el-footer height="auto" v-if="editTabsData.length">
            <slot name="otherForm"></slot>
          <el-tabs :type="tabsType">
            <el-tab-pane v-for="(item,index) in editTabsData" :label="item.label">
                <cool-form-view ref="loopCoolForm" :form-items="item.formItems"  :inline="item.inline" :size="item.size" :itemWidth="item.itemWidth" :itemStyle="item.itemStyle" :labelWidth="item.labelWidth" :rules="item.rules" :labelPosition="item.labelPosition" @update-form="tabsUpdateForm(item.tabsUpdateForm)" @input-button-event="mixInputButtonEvent" @click-events="itemClick" @select="handleSelect" @query-search="querySearch">
                </cool-form-view>
            </el-tab-pane>
      </el-tabs>
        </el-footer>
      </el-container>
        <el-button :icon="backBtnIcon" @click="backEvent" :size="BtnSize" :type="backBtnType">{{backBtnText}}</el-button>
        <el-button :icon="saveBtnIcon" @click="saveEvent" :size="BtnSize" style="float:right;" :type="saveBtnType">{{saveBtnText}}</el-button>
    </el-dialog>
</template>

<script>
    // import coolForm from './cool-form.vue'
const component = {
    name: 'edit-dialog',
    components:{
        // coolForm
    },
    props: {
        //dialog
        visible: Boolean,
        top: String,
        width:String,
        editDialogTitle:String,
        // cool-form
        formItems: Object,
        size: {
            type:String,
            default:'mini'
        },
        inline: {
            type:Boolean,
            default:true
        },
        itemWidth: String,
        itemStyle: Object,
        labelWidth: String,
        rules: Object,
        labelPosition: String,
        // tabs
        tabsType:{
            type:String,
            default:'card'
        },
        editTabsData:{
          type:Array,
          default:function(){
            return []
          }
        },
        // closeBtn
        closeBtnMini:{
            type:String,
            default:'mini'
        },
        closeBtnType:{
            type:String,
            default:'danger'
        },
        // footerBtn
        backBtnIcon:{
            type:String,
            default:'el-icon-back'
        },
        saveBtnIcon:{
            type:String,
            default:'el-icon-document'
        },
        BtnSize:{
            type:String,
            default:'mini'
        },
        backBtnText:{
            type:String,
            default:'返回'
        },
        saveBtnText:{
            type:String,
            default:"保存"
        },
        backBtnType:String,
        saveBtnType:String
    },
    data:function(){
      return {
        // container style
        containerStyle:'border:1px solid #DCDFE6',
        // closeBtn
        closeBtnStyle:'width:24px; padding:4px;'
      }
    },
    install: (Vue, options) =>
        Vue.component(
            (options ? options.prefix : '') + component.name,
            component,
        ),
    methods:{
        // dialog
       closeClicked: function() {
        this.$emit('update:visible', !this.visible) //emit for sync
        },
        closedialog() {
          this.$emit('closedialog') //emit for sync
        },
        // cool-form
        updateForm(obj,arg,label) {
        // console.log(arg)
          this.$emit('update-form', obj,arg,label)
        },
        tabsUpdateForm(arg){
            // console.log(arg)
          this.$emit('tabs-update-form', arg)
        },
        mixInputButtonEvent(arg){
          this.$emit('input-button-event',arg)
        },
        itemClick(ev) {
          this.$emit('click-events', ev)
        },
        querySearch(queryString, cb) {
          this.$emit('query-search', queryString, cb)
        },
        handleSelect(item) {
          this.$emit('select', item)
          this.updateForm()
        },
         validateForm(arg){
          this.$refs.coolForm.validateForm()
          if(this.editTabsData.length>0){
            for(let i = 0;i<this.editTabsData.length - 1;i++){
             this.$refs.loopCoolForm[i].validateForm()
          }
          }
        },
        clearForm(){
          this.$refs.coolForm.clearForm()
          if(this.editTabsData.length>0){
            for(let i = 0;i<this.editTabsData.length;i++){
             this.$refs.loopCoolForm[i].clearForm()
          }
          }

        },
        resetForm(){
          this.$refs.coolForm.resetForm()
          if(this.editTabsData.length>0){
            for(let i = 0;i<this.editTabsData.length - 1;i++){
             this.$refs.loopCoolForm[i].resetForm()
          }
          }
        },
        keyupEnter(){
          this.$emit('keyup-enter')
        },
        keyupUp(){
          this.$emit('keyup-up')
        },
        keyupDown(){
          this.$emit('keyup-down')
        },
        // button
        backEvent(arg){
            this.$emit('update:visible', !this.visible)
        },
        saveEvent(arg){
            this.$emit('save-event',arg)
        }
    }
}

export default component
</script>

<style scoped>

</style>
