<template>
	<div>
		<template v-for="dialog in dialogs">
            <cool-dialog class="rawdialog" :name="dialog.name" :id="dialog.id" :visible.sync="dialog.visible" :fullscreen.sync="dialog.fullscreen" :collapsed.sync="dialog.collapsed" :width="dialog.width" :iframe-height="dialog.iframeHeight" :title="dialog.title" :src.sync="dialog.src" :top="dialog.top"  @closedialog="closedialog(dialog.name)" :backBtnIcon="dialog.backBtnIcon" :saveBtnIcon="dialog.saveBtnIcon" :BtnSize="dialog.BtnSize" :backBtnText="dialog.backBtnText" :saveBtnText="dialog.saveBtnText" :backBtnType="dialog.backBtnType" :saveBtnType="dialog.saveBtnType" :showSaveButton="dialog.showSaveButton" :saveBtnDisabled="dialog.saveBtnDisabled" @back-event="backEvent"  @save-event="saveEvent" :before-close="beforeClose"></cool-dialog>
    	</template>
	</div>
</template>

<script>
	import coolDialog from './commonComponents/cool-dialog.vue'
	const component = {
		name: 'multi-dialog',
		components: {
			coolDialog
		},
		props: {
		    dialogs: Array,
				// saveBtnDisabled: [Boolean, Function]
		  },
		  install: (Vue, options) =>
        Vue.component(
            (options ? options.prefix : '') + component.name,
            component,
        ),
		methods: {
		    closedialog(name) {
		      this.$emit('closedialog',name)
		    },
		     backEvent(){
		        this.$emit('back-event')
		    },
		    saveEvent(){
		      this.$emit('save-event')
		    },
		    beforeClose(){
		    	this.$emit('before-close')
		    }
		  }
	}
	export default component
</script>

