<template>
    <el-container>
      <el-header height="auto">
         <el-header :style="hdrHeaderStyle" height="auto">
               {{hdrHeaderText}}
         </el-header>
        <cool-form-view ref="coolForm" :form-items="formItems"  :inline="inline" :size="size" :itemWidth="itemWidth" :itemStyle="itemStyle" :labelWidth="labelWidth" :rules="rules" :labelPosition="labelPosition" @update-form="updateForm" @input-button-event="mixInputButtonEvent" @click-events="itemClick" @select="handleSelect" @query-search="querySearch"></cool-form-view>
      </el-header>
      <el-main>
        <el-container>
          <el-header :style="dltHeaderStyle" height="auto">
          {{dltHeaderText}}
          <slot name="dltHeaderAdd"></slot>
        </el-header>
        <el-main>
          <table-view ref="newEditTable"  :urlUploadAttachment="newEditTableData.urlUploadAttachment" :uploadTool="newEditTableData.uploadTool"  :uploadData="newEditTableData.uploadData" :multiple="newEditTableData.multiple" :name="newEditTableData.name" :withCredentials="newEditTableData.withCredentials" :showFileFist="newEditTableData.showFileFist" :drag="newEditTableData.drag" :accept="newEditTableData.accept" :onPreview="newEditTableData.onPreview" :onRemove="newEditTableData.removeChange" :onError="newEditTableData.onError" :onProgress="newEditTableData.onProgress" :onChange="newEditTableData.onChange" :beforeRemove="newEditTableData.beforeRemove" :listType="newEditTableData.listType" :autoUpload="newEditTableData.autoUpload" :httpRequest="newEditTableData.httpRequest" :uploadDisabled="newEditTableData.uploadDisabled" :limit="newEditTableData.limit" :onExceed="newEditTableData.onExceed" :uploadlist="newEditTableData.uploadlist"  :uploadButtons="newEditTableData.uploadButtons" :uploadButtonSize="newEditTableData.uploadButtonSize" :headers="newEditTableData.headers" :showSummary="newEditTableData.showSummary" :buttonGroupStyle="newEditTableData.buttonGroupStyle" :buttonGroupClass="newEditTableData.buttonGroupClass" :tableClass="newEditTableData.tableClass" :tableStyle="newEditTableData.tableStyle" :paginationClass="newEditTableData.paginationClass" :paginationStyle="newEditTableData.paginationStyle" :vLoading="newEditTableData.vLoading" :stripe="newEditTableData.stripe" :size="newEditTableData.size" :width="newEditTableData.width" :height="newEditTableData.height" :maxHeight="newEditTableData.maxHeight" :fit="newEditTableData.fit" :rowKey="newEditTableData.rowKey" :showPage="newEditTableData.showPage" :border="newEditTableData.border" :data="newEditTableData.data" :columns="newEditTableData.columns" :total="newEditTableData.total" :layout="newEditTableData.layout" :pageSize.sync="newEditTableData.pageSize" :currentPage.sync="newEditTableData.currentPage" :context="newEditTableData.context" :showHeader="newEditTableData.showHeader" :sumText="newEditTableData.sumText" :summaryMethod="newEditTableData.summaryMethod" :rowClassName="newEditTableData.rowClassName" :rowStyle="newEditTableData.rowStyle" :cellClassName="newEditTableData.cellClassName" :cellStyle="newEditTableData.cellStyle" :headerRowClassName="newEditTableData.headerRowClassName" :headerRowStyle="newEditTableData.headerRowStyle" :headerCellClassName="newEditTableData.headerCellClassName" :headerCellStyle="newEditTableData.headerCellStyle" :highlightCurrentRow="newEditTableData.highlightCurrentRow" :currentRowKey="newEditTableData.currentRowKey" :emptyText="newEditTableData.emptyText" :expandRowKeys="newEditTableData.expandRowKeys" :defaultExpandAll="newEditTableData.defaultExpandAll" :defaultSort="newEditTableData.defaultSort" :tooltipEffect="newEditTableData.tooltipEffect" :spanMethod="newEditTableData.spanMethod" :buttonSize="newEditTableData.buttonSize":buttonPlain="newEditTableData.buttonPlain" :buttonRound="newEditTableData.buttonRound" :tableButtons="newEditTableData.tableButtons" :showTableButton="newEditTableData.showTableButton" :pageCount="newEditTableData.pageCount" :pageSizes="newEditTableData.pageSizes" :popperClass="newEditTableData.popperClass" :prevText="newEditTableData.prevText" :nextText="newEditTableData.nextText" :background="newEditTableData.background" @table-row-click="newEditTableRowClick" @table-select="newEditTableSelect" @table-select-all="newEditTableSelectAll" @table-selection-change="newEditTableSelectionChange" @table-cell-click="newEditTableCellClick" @table-row-dblclick="newEditTableRowDbclick" @table-header-click="newEditTableHeaderClick" @table-sort-change="newEditTableSortChange" @table-current-change="newEditTableCurrentChange" @pagination-size-change="newEditPaginationSizeChange" @pagination-current-change="newEditPaginationCurrentChange"  @table-buttons-event="newEditTableButtonsEvent" >
        </table-view>
        </el-main>
        </el-container>
      </el-main>
      <el-footer v-if="showFooter" height="auto">
      <el-button type="danger" size="mini" @click="cancelClick">取消</el-button>
      <el-button type="success" size="mini" :style="'float:right'" @click="maintainClick" :disabled="maintainBtn">{{maintainText}}</el-button>
    </el-footer>
    </el-container>
</template>

<script>
	// import coolForm from './cool-form.vue'
	import coolTable from './cool-table.vue'
const component = {
    name: 'new-edit-views',
    components:{
    	// coolForm,
    	coolTable
    },
    props: {
    	// cool-form
    	formItems: Object,
    	size: {
    		type:String,
    		default:'mini'
    	},
	    inline: {
	    	type:Boolean,
	    	default:true
	    },
	    itemWidth: String,
	    itemStyle: Object,
	    labelWidth: String,
	    rules: Object,
	    labelPosition: String,
	    // 头部信息文字、
	    hdrHeaderText:String,
	    dltHeaderText:String,
	    // footer
	    maintainText:{
	    	type:String,
	    	default:'保存'
	    },
      	maintainBtn:{
      		type:Boolean,
      		default:true
      	},
      	showFooter:{
      		type:Boolean,
      		default:true
      	},
      	// table
      	newEditTableData:{
          type:Object,
          default:function(){
            return {}
          }
        },
    },
    install: (Vue, options) =>
        Vue.component(
            (options ? options.prefix : '') + component.name,
            component,
        ),
    methods:{
    	// cool-form
    	updateForm(obj,arg,label) {
	      this.$emit('update-form', obj,arg,label)
	    },
	    mixInputButtonEvent(arg){
      	  this.$emit('input-button-event',arg)
	    },
	    itemClick(ev) {
	      this.$emit('click-events', ev)
	    },
	    querySearch(queryString, cb) {
	      this.$emit('query-search', queryString, cb)
	    },
	    handleSelect(item) {
	      this.$emit('select', item)
	      this.updateForm()
	    },
	    validateForm(){
	      this.$refs.coolForm.validateForm()
	    },
	    clearForm(){
	      this.$refs.coolForm.clearForm()
	    },
	    resetForm(){
	      this.$refs.coolForm.resetForm()
	    },
	    //footer button
	    cancelClick(){
	      this.$emit('cancel-click')
	    },
	    maintainClick(){
	      this.$emit('maintain-click')
	    },
	    // table方法
	    newEditTableRowClick(arg){
	      this.$emit('new-edit-row-click',arg)
	    },
	    newEditTableSelect(arg){
	      this.$emit('new-edit-select',arg)
	    },
	    newEditTableSelectAll(arg){
	      this.$emit('new-edit-select-all',arg)
	    },
	    newEditTableSelectionChange(arg){
	      this.$emit('new-edit-select-change',arg)
	    },
	    newEditTableCellClick(arg){
	      this.$emit('new-edit-cell-click',arg)
	    },
	    newEditTableRowDbclick(arg){
	      this.$emit('new-edit-row-dbclick',arg)
	    },
	    newEditTableHeaderClick(arg){
	      this.$emit('new-edit-header-click',arg)
	    },
	    newEditTableSortChange(arg){
	      this.$emit('new-edit-sort-change',arg)
	    },
	    newEditTableCurrentChange(arg){
	      this.$emit('new-edit-current-change',arg)
	    },
	    newEditPaginationSizeChange(arg){
	      this.$emit('new-edit-page-size-change',arg)
	    },
	    newEditPaginationCurrentChange(args){
	      this.$emit('new-edit-pagination-current-change',args)
	    },
	    newEditClearSelectionOuter(){
	        this.$refs.newEditTable.clearSelectionOuter();
	    },
	    newEditClearCurrentRow(){
	        this.$refs.newEditTable.clearCurrentRow();
	    },
	    newEditTableButtonsEvent(arg){
	      this.$emit('new-edit-table-buttons-event',arg)
	    },

    }
}

export default component
</script>

<style scoped>

</style>
